.CompactCard {
  display: flex;
  flex: 1;
  height: 7rem !important;
  border-radius: 0.7rem;
  color: white;
  padding: 1rem;
  position: relative;
  cursor: pointer;
  margin-top: -170px; 
  color: rgb(10, 9, 9);
 
  font-weight: bold;
}
.CompactCard:hover {
  box-shadow: none !important;
}

/* radial bar */
.radialBar {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
  color:rgb(71, 69, 69);
}
.CircularProgressbar {
  width: 4rem !important;
  overflow: visible;
  color: rgb(20, 19, 19);

}
.CircularProgressbar-path {
  stroke: white !important;
  stroke-width: 12px !important;
  filter: drop-shadow(2px 4px 6px white);
}
.CircularProgressbar-trail {
  display: none;
}
.CircularProgressbar-text {
  fill: white !important;
}

.radialBar > span {
  font-size: 17px;
  font-weight: bold;
  color:rgb(71, 69, 69);
}

/* sideDetail*/
.detail {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.detail > span:nth-child(2) {
  font-size: 15px;
  font-size: small;
  font-weight: bold;
  color: rgb(20, 19, 19);
}
.detail > span:nth-child(3) {
  font-size: 15px;
  font-size: small;
  font-weight: bold;
  color: rgb(20, 19, 19);
}

